import React from 'react';

interface Task {
  id: string;
  text: string;
  state: 'ToDo' | 'InProgress' | 'Completed';
}

function KanbanBoard() {
  const [tasks, setTasks] = React.useState<Task[]>([]);

  // Later you'll fetch tasks here
  React.useEffect(() => {
    // Fetch tasks
  }, []);

  const todoTasks = tasks.filter(task => task.state === 'ToDo');
  const inProgressTasks = tasks.filter(task => task.state === 'InProgress');
  const completedTasks = tasks.filter(task => task.state === 'Completed');

  return (
    <div className="p-4">
      <div className="flex gap-4">
        {/* To Do Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">To Do</h2>
          <div className="min-h-[500px]">
            {todoTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>

        {/* In Progress Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">In Progress</h2>
          <div className="min-h-[500px]">
            {inProgressTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>

        {/* Completed Column */}
        <div className="bg-gray-100 p-4 rounded-lg w-80">
          <h2 className="font-bold mb-4 text-black">Completed</h2>
          <div className="min-h-[500px]">
            {completedTasks.map(task => (
              <div key={task.id} className="bg-white p-4 mb-2 rounded shadow">
                {task.text}
              </div>
            ))}
          </div>
        </div>
      </div>
      </div>
    );
  }


export default KanbanBoard;
